<template>
  <div>
    <v-card>
      <v-card-text>
        <v-form ref="form" v-model="valid">
          <v-text-field v-model="createReq.name" label="" required :rules="[ v => !!v || '', ]" class="mb-3"></v-text-field>
          <v-text-field v-model.number="createReq.price" label="" required type="number" prefix="$" :rules="[ v => !!v || '', ]" class="mb-3"></v-text-field>
          <v-text-field v-model="createReq.loc" label="" required hint=":28"  :rules="[ v => !!v || '?', ]" class="mb-3"></v-text-field>

          <v-btn class="primary" @click="submit"></v-btn>
        </v-form>
      </v-card-text>

    </v-card>
  </div>
</template>

<script>
import { createPost } from "@/apis/animal";

export default {
  data() {
    return {
      valid: true,
      createReq: {
        name: '',
        price: '',
        loc: ''
      },
      test: ''
    }
  },
  methods: {
    async submit() {
      if(this.$refs.form.validate()) {
        await createPost({
          ...this.createReq
        })

        this.$message.success('')
        this.$refs.form.reset()
        this.$route.push('/')
      }
    },

  }
}
</script>
