//axios
export const throwErr = (code, response) => {
    let message = ''
    switch (code) {
        case -1:
            message = 'appkey'
            break
        case -3:
            message = ''
            break
        case -101:
            message = ''
            break
        case -400:
            message = ''
            break
        case -413:
            message = ''
            break
        case -444:
            message = ''
            break
        case -500:
            message = ''
            break
        case 400:
            message = ''
            break
        case 401:
            message = ''
            break
        case 403:
            message = ''
            break
        case 404:
            message = '~' //: ${response.config.url}
            break
        case 408:
            message = ''
            break
        case 500:
            message = ''
            break
        case 501:
            message = ''
            break
        case 502:
            message = ''
            break
        case 503:
            message = ''
            break
        case 504:
            message = ''
            break
        case 505:
            message = 'HTTP'
            break
        default:
    }
    return message
}

//jsonurl
export const j2UrlParams = (json) => {
    return Object.keys(json).map((key) => encodeURIComponent(key) + "=" + encodeURIComponent(json[key])).join('&')
}

//
//let sortObj = infoObj.sort(compare("age"))
export const compare = (property) => {
    return function(obj1,obj2){
        let value1 = obj1[property]
        let value2 = obj2[property]
        return value1 - value2  // 
    }
}

//mapv-select items
export const m2SelectItems = (data) => {
    return [ ...data.entries() ].map(item => ({text: item[1], value: item[0]}))
}